
import 'chartjs-adapter-moment';

import Modal from "@/components/common/Modal.vue";

import {defineComponent, onMounted, ref} from "vue";
import CustomSelect from "@/components/common/CustomSelect.vue";
import moment, {Moment} from "moment";
import Remote from "@/remote/scheduleRequest";
import Dialog from "@/components/dialog";
import {EmployeeActions} from "@/store/employeeModule";
import {useStore} from "vuex";
import DatePicker from "@/components/common/DatePicker.vue";

declare var Affinity: any
let MyChart = <any>null;
export default defineComponent ({
    name: "AnalysisV2",
    components: {
        Modal,
        CustomSelect,
        DatePicker,
    },

    setup() {
        const store = useStore()
        //
        const data = ref(<any[]>[])
        const pageNumber = ref(1)
        const pageSize = ref(20)
        const totalPages = ref(5)
        const pageList = ref(<number[]> []);
        const DatePickerRef = ref<InstanceType<typeof DatePicker>>();

        //
        const fromDate = ref(<Moment | null>null);
        const toDate = ref(<Moment | null>null);
        const employeeList = ref(<number[]> []);
        const fromDateText = ref('');
        const toDateText = ref('');
        const dateModalTop = ref(0);
        const dateModalLeft = ref(0);
        const datePickerMax = ref(<Moment | null>null);
        const datePickerMin = ref(<Moment | null>null);
        const datePickerTargetDay = ref( <Moment | null>moment())

        const dataHeadings = ref([
            "Employee Name",
            "Employee Number",
            "Functional Area",
            "Date",
            "Award",
            "Scheduled Hours",
            "Actual Hours",
            "Hours Variance",
            "Scheduled Cost",
            "Actual Cost",
            "Cost Variance",
            "Actual Start Time",
            "Actual End Time",
            "Adjusted Start Time",
            "Adjusted End Time",
        ])

        const dataAnalysis = ref(<any[]>[]);
        const options = ref([
            "Start/End Time",
            "Detailed Cost Report"
        ]);

        onMounted(() => {
            initialRequest()
        })

        async function initialRequest() {
            Dialog.loading()
            await store.dispatch(EmployeeActions.getEmployees, Affinity.user.EmployeeNo).then((res: any) => {
                employeeList.value = res.map((employeeDetail: any) => employeeDetail.employeeNo)
            })
            await loadGridData()

            Dialog.clear();
        }

        async function loadGridDataWithDate() {
            Dialog.loading()
            pageNumber.value = 1;
            await loadGridData()
            Dialog.clear();
        }

        async function loadGridData() {
            if (fromDate.value && toDate.value) {
                if (toDate.value.diff(fromDate.value, 'month')) {
                    Dialog.alert('Selected Date range must not greater then 6 month');
                    return;
                }
            }

            if (!fromDate.value || !toDate.value) {
                if (!fromDate.value) {
                    fromDate.value = moment().startOf('month')
                }
                if (!toDate.value) {
                    toDate.value = moment().endOf('month')
                }
            }

            const query = {
                pageNumber: pageNumber.value,
                pageSize: pageSize.value,
                pagingEnabled: true,
                employeeNumbers: employeeList.value,
                transDateFrom: fromDate.value ? fromDate.value.format('YYYY-MM-DD') : null,
                transDateTo: toDate.value ? toDate.value.format('YYYY-MM-DD') : null,
            }
            
            await Remote.GetCostAnalysisV2(query).then((res: any) => {
                dataAnalysis.value = res.response.map((e: any) => {
                    const a: any = {};
                    a.commonName = e.commonName;
                    a.employeeNo = e.employeeNo;
                    a.functionalArea = e.functionalArea;
                    a.transDate = moment(e.transDate).format('YYYY-MM-DD')
                    a.awardId = e.awardId;
                    a.scheduledHours = e.scheduledUnits;
                    a.actualHours = e.actualUnits;
                    a.hoursVariance =  Math.round( a.actualHours - a.scheduledHours * 100) / 100 ;
                    a.scheduledCost = e.scheduledCost ? Math.round(e.scheduledCost * 100) / 100 : null;
                    a.actualCost = e.actualCost ? Math.round(e.actualCost * 100) / 100 : null;
                    a.costVariance = Math.round( a.actualCost - a.scheduledCost * 100) / 100
                    a.actStartTime = e.actStartTime ? moment(e.actStartTime).format('hh:mm') : null;
                    a.actEndTime = e.actEndTime ? moment(e.actEndTime).format('hh:mm') : null;
                    a.adjStartTime = e.adjStartTime ? moment(e.adjStartTime).format('hh:mm') : null;
                    a.adjEndTime = e.adjEndTime ? moment(e.adjEndTime).format('hh:mm') : null;

                    return a;
                })
                totalPages.value = res.totalPages
                pageNumber.value = res.currentPageNumber

                calculatePageList(res.currentPageNumber, res.totalPages)
            })
        }

        function calculatePageList(pageNumber: number, pageSize: number) {
            if (pageSize <= 5) {
                pageList.value = Array.from(Array(pageSize).keys()).map(k => k + 1)
            } else {
                if (pageNumber <= 3) {
                    pageList.value = Array.from(Array(5).keys()).map(k => k + 1)
                } else {
                    if ( pageSize > pageNumber + 2) {
                        pageList.value = Array.from(Array(5).keys()).map(k => k + pageNumber - 2)
                    } else {
                        pageList.value = Array.from(Array(5).keys()).map(k => k + pageSize - 4)
                    }
                }
            }
        }

        async function updateToDateTime($event: any, date: any) {
            let element = $event.target.getClientRects()[0]
            dateModalTop.value = element.bottom;
            dateModalLeft.value = element.left;
            if (fromDate) {
                datePickerMin.value = fromDate.value;
                datePickerMax.value = null;
            }
            datePickerTargetDay.value = toDate.value ? toDate.value : moment();

            const d  = await openDatePicker()
            toDate.value = d || undefined
            toDateText.value = toDate.value!.format("DD/MM/YYYY")
        }

        async function updateFromDateTime($event: any, date: any) {
            let element = $event.target.getClientRects()[0]
            dateModalTop.value = element.bottom;
            dateModalLeft.value = element.left;

            if (toDate) {
                datePickerMin.value = null;
                datePickerMax.value = toDate.value;
            }

            datePickerTargetDay.value = fromDate.value ? fromDate.value : moment();

            const d = await openDatePicker()
            fromDate.value = d || undefined
            fromDateText.value = fromDate.value!.format("DD/MM/YYYY")
        }

        async function selectPage(newPageNumber: number) {
            pageNumber.value = newPageNumber
            Dialog.loading()
            await loadGridData()
            Dialog.clear()
        }

        async function selectNextPage(pageDiff: number = 1) {
            pageNumber.value = pageNumber.value + pageDiff
            Dialog.loading()
            await loadGridData()
            Dialog.clear()
        }

        async function openDatePicker() {
           return await(DatePickerRef.value!.toggleShow(datePickerTargetDay.value!)) as Promise<any>;
        }

        async function exportCSV() {
            Dialog.loading();
            const allData: any = await loadAllData();
            Dialog.clear();
            let csvContent = "data:text/csv;charset=utf-8,";

            csvContent += dataHeadings.value.join(',') + "\r\n";

            allData.map((rowArray: any) => {
                csvContent += Object.values(rowArray).join(',') + "\r\n";
            });

            var encodedUri = encodeURI(csvContent);
            var link = document.createElement("a");
            link.setAttribute("href", encodedUri);
            link.setAttribute("download", "Analytic.csv");
            document.body.appendChild(link); // Required for FF

            link.click();
        }

        async function loadAllData() {

            const query = {
                pageNumber: 1,
                pageSize: 10000,
                pagingEnabled: false,
                employeeNumbers: employeeList.value,
                transDateFrom: fromDate.value ? fromDate.value.format('YYYY-MM-DD') : null,
                transDateTo: toDate.value ? toDate.value.format('YYYY-MM-DD') : null,
            }
            return await Remote.GetCostAnalysisV2(query).then((res: any) => {
                res.response.sort((a: any, b: any) => {
                    const nameA = a.commonName.toUpperCase();
                    const nameB = b.commonName.toUpperCase();
                    if (nameA < nameB) {
                        return -1;
                    }
                    if (nameA > nameB) {
                        return 1;
                    }

                    // names must be equal
                    return 0;
                });


                return res.response.map((e: any) => {
                    const a: any = {};
                    a.commonName = e.commonName;
                    a.employeeNo = e.employeeNo;
                    a.functionalArea = e.functionalArea;
                    a.transDate = moment(e.transDate).format('YYYY-MM-DD')
                    a.awardId = e.awardId;
                    a.scheduledHours = e.scheduledUnits;
                    a.actualHours = e.actualUnits;
                    a.hoursVariance = a.actualHours - a.scheduledHours;
                    a.scheduledCost = Math.round(e.scheduledCost * 100) / 100;
                    a.actualCost = Math.round(e.actualCost * 100) / 100;
                    a.costVariance = a.actualCost - a.scheduledCost
                    a.actStartTime = e.actStartTime ? moment(e.actStartTime).format('hh:mm') : null;
                    a.actEndTime = e.actEndTime ? moment(e.actEndTime).format('hh:mm') : null;
                    a.adjStartTime = e.adjStartTime ? moment(e.adjStartTime).format('hh:mm') : null;
                    a.adjEndTime = e.adjEndTime ? moment(e.adjEndTime).format('hh:mm') : null;

                    return a;
                })
            })
        }

        return {
            DatePickerRef,
            data, dataHeadings, dataAnalysis, options,
            pageNumber, pageSize, totalPages, pageList,
            dateModalTop, dateModalLeft, datePickerMax, datePickerMin, datePickerTargetDay,
            fromDate, toDate, fromDateText, toDateText,
            updateToDateTime, updateFromDateTime, loadGridData, exportCSV, selectPage, loadGridDataWithDate,
            selectNextPage}
    }
})
